  methods: {
    onSubmit(formName) {
      // console.log('submit!', this.form)
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          let data
          this.isSubmit = true
          if (this.isCreate) {
            data = await this.$axios
              .$post('/api/admin/commodity/create', this.form)
              .catch((error) => {
                this.isSubmit = false
                this.$message({
                  showClose: true,
                  message: `${this.typeText}${this.$t('content.fail')}! ${
                    error.response.data.message
                  }`,
                  type: 'error',
                })
              })
          } else {
            // console.log('this.form', this.form)
            data = await this.$axios
              .$post('/api/admin/commodity/update', this.form)
              .catch((error) => {
                this.isSubmit = false
                this.$message({
                  showClose: true,
                  message: `${this.typeText}${this.$t('content.fail')}! ${
                    error.response.data.message
                  }`,
                  type: 'error',
                })
              })
          }
          this.isSubmit = false
          // console.log('data', data)
          if (data.success) {
            this.$message({
              showClose: true,
              message: `${this.typeText}${this.$t('content.success')}`,
              type: 'success',
            })
            if (this.isCreate) {
              // this.$refs[formName].resetFields()
              // this.resetForm('form')
              this.isCreate = false
              this.$router.push({
                query: {
                  commodityId: data.data.commodityId,
                },
              })
            }
            // window.location.reload();
          } else {
            this.$message({
              showClose: true,
              message: `${this.typeText}${this.$t('content.fail')}!${
                data.message
              }`,
              type: 'error',
            })
          }
        }
      })
    },

    onMock() {
      if (
        !this.categorys.length ||
        !this.classifications.length ||
        !this.materials.length ||
        !this.models.length ||
        !this.places.length ||
        !this.ruiwus.length ||
        !this.shapes.length ||
        !this.specifications.length ||
        !this.styles.length ||
        !this.techniques.length ||
        !this.themes.length ||
        !this.types.length ||
